
import { Item, Order } from "@/interface";
import { rounding } from "@/mixins/generalMixin";
import { defineComponent, ref } from "vue";

export default defineComponent({
  props: ["isShowOrderHistoryModal", "orderHistories", "systemData"],
  emits: ["showOrderHistoryModal"],
  // setup(props) {
  //   let orderHistories = ref<Order[]>();
  //   console.log(props.isShowOrderHistoryModal)
  //   console.log(props.orderHistories)

  //   if (props.isShowOrderHistoryModal && props.orderHistories.length > 0) {
  //     const dataOrderHistories = props.orderHistories;
  //     dataOrderHistories.forEach((order: Order) => {
  //       order.items = JSON.parse(order.items);
  //     });
  //     orderHistories.value = dataOrderHistories;
  //   }
  //   return { orderHistories };
  // },
  computed: {
    totalAmount() {
      return this.orderHistories[0].items.reduce(
        (total: number, item: Item) => {
          total += item.price * item.added;
          return total;
        },
        0
      );
    },
    serviceCharge() {
      return this.systemData.serviceCharge10Percent
        ? this.totalAmount * 0.1
        : 0;
    },
    tax() {
      return this.systemData.tax6Percent ? this.totalAmount * 0.06 : 0;
    },
    roundingAdj() {
      const serviceCharge = parseFloat(this.serviceCharge.toFixed(2));
      const tax = parseFloat(this.tax.toFixed(2));
      return rounding(this.totalAmount + serviceCharge + tax);
    },
    nettTotal() {
      return (
        this.totalAmount + this.serviceCharge + this.tax + this.roundingAdj
      );
    },
  },
  methods: {
    padStart(val: number) {
      return val.toString().padStart(3, "0");
    },
    showOrderHistoryModal(value: boolean) {
      this.$emit("showOrderHistoryModal", value);
    },
  },
});
