
import { Item } from "@/interface";
import { defineComponent } from "vue";

export default defineComponent({
  props: ["systemData", "banners", "categories"],
  emits: ["showItemDetail", "addToOrder"],
  mounted(){
    window.document.title = `${this.systemData.companyName} - Menu Panda`;
  },
  methods: {
    toPages(items: Item[]) {
      let results: Item[][] = [];
      let slot = null;
      for (let i = 0; i < items.length; i++) {
        if (i % 3 === 0) {
          results.push([]);
          slot = Math.floor(i / 3);
        }
        results[slot!].push(items[i]);
      }
      return results;
    },
    showItemDetail(id: number) {
      this.$emit("showItemDetail", id);
    },
    addToOrder(id: number) {
      this.$emit("addToOrder", { id, qty: 1 });
    },
  },
});
