
import { Item } from "@/interface";
import { defineComponent } from "vue";

export default defineComponent({
  props: ["diplayItemId", "items", "totalAmount"],
  emits: ["closeItemDetail", "addToOrder"],
  data() {
    return {
      item: {} as Item,
    };
  },
  methods: {
    addToOrder(id: number) {
      this.$emit("addToOrder", { id, qty: 1 });
      setTimeout(() => {
        this.close();
      }, 500);
    },
    close() {
      this.$emit("closeItemDetail");
    },
  },
  watch: {
    diplayItemId(newVal) {
      if (newVal !== null) {
        this.item = this.items.find((i: Item) => i.id === newVal);
      }
    },
  },
});
