import { createRouter, createWebHashHistory, RouteRecordRaw, RouterScrollBehavior } from 'vue-router'
import HomeView from '../views/HomeView.vue'

type ScrollPositionNormalized = {
  behavior?: ScrollOptions['behavior']
  left: number
  top: number
}

declare module 'vue-router' {
  interface RouteMeta {
    scrollPos?: ScrollPositionNormalized
  }
}

const APP_NAME = 'Menu Panda';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      scrollPos: {
        top: 0,
        left: 0,
      },
    },
  },
  {
    path: '/c/:categoryId',
    name: 'category',
    component: () => import('../views/CategoryView.vue'),
  },
  {
    path: '/o',
    name: 'orderList',
    component: () => import('../views/OrderListView.vue'),
    meta: {
      title: `Order - ${APP_NAME}`
    },
  },
  {
    path: '/i',
    name: 'info',
    component: () => import('../views/InfoView.vue'),
    meta: {
      title: `Info - ${APP_NAME}`
    }
  },
]

const scrollBehavior: RouterScrollBehavior = (to, from, savedPosition) => {
  if (to.name === from.name) {
    to.meta?.scrollPos && (to.meta.scrollPos.top = 0)
    return { left: 0, top: 0 }
  }
  const scrollpos = savedPosition || to.meta?.scrollPos || { left: 0, top: 0 }
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve(scrollpos)
    }, 500)
  })
}

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior,
})

router.beforeEach((to, from, next) => {
  if (typeof (to.meta.title) === 'string') {
    window.document.title = to.meta.title;
  }

  from.meta?.scrollPos && (from.meta.scrollPos.top = document.documentElement.scrollTop)
  return next()
})

export default router
