function padTo2Digits(num: number) {
    return num.toString().padStart(2, "0");
}

export function formatToGoogleSheetDate(date: Date) {
    return [
        padTo2Digits(date.getDate()),
        padTo2Digits(date.getMonth() + 1),
        date.getFullYear(),
    ].join("/") +
        " " +
        [
            padTo2Digits(date.getHours()),
            padTo2Digits(date.getMinutes()),
            padTo2Digits(date.getSeconds()),
        ].join(":")
}


export function formatDate(date: Date) {
    return [
        date.getFullYear(),
        padTo2Digits(date.getMonth() + 1),
        padTo2Digits(date.getDate()),
    ].join("-") +
        " " +
        [
            padTo2Digits(date.getHours()),
            padTo2Digits(date.getMinutes()),
            padTo2Digits(date.getSeconds()),
        ].join(":")
}

export function formatTimeDate(date: Date) {
    const hourIn24 = date.getHours();
    const amPm = hourIn24 > 13 ? 'pm' : 'am';
    return [
        (hourIn24 > 13 ? padTo2Digits(hourIn24 - 12) : padTo2Digits(hourIn24)),
        padTo2Digits(date.getMinutes()),
        padTo2Digits(date.getSeconds()),
    ].join(":") +
        " " + amPm + " " +
        [
            padTo2Digits(date.getDate()),
            padTo2Digits(date.getMonth() + 1),
            date.getFullYear().toString().substring(2, 4),
        ].join("/")
}


export function dateToString(value: string) {
    if (!value.includes('Date')) return value;
    const dateString = value.substring(5, value.length - 1);
    const splits = dateString.split(",");
    return formatDate(
        new Date(
            +splits[0],
            +splits[1],
            +splits[2],
            +splits[3],
            +splits[4],
            +splits[5]
        )
    );
}
