import { CRUD } from "@/interface";

export default function fetchPost<T>(crud: CRUD, sheet: string, url: string, data: T) {
    // data type
    const dataType = {} as any;
    for (const key in data) {
        dataType[key] = typeof data[key];
    }

    const postData = { crud, sheet, data, dataType };

    let fetchPromise = fetch(
        `https://script.google.com/macros/s/${url}/exec`,
        {
            method: "POST",
            mode: "no-cors",
            cache: "no-cache",
            headers: {
                "Content-Type": "application/json",
            },
            redirect: "follow",
            body: JSON.stringify(postData),
        }
    );
    return fetchPromise;
    //.then((_rep) => { console.log('done') }).catch(() => alert("fail to update"));
}
