export interface System {
    id: number
    companyName: string
    companyAddress: string
    companyContact: string
    companyDescription: string
    serviceCharge10Percent: boolean
    tax6Percent: boolean
}

export interface Banner {
    id: number
    image: string
    description: string
    itemId: number
    item?: Item
}

export interface Item {
    id: number
    category: string
    image: string
    code: string
    name: string
    description: string
    price: number
    added: number
}

export interface Category {
    id: number
    name: string
    items: Item[]
}

export interface Order {
    id: number
    orderId?: number
    status: OrderStatus
    tableNo: number
    items: string
    amount: number
    createdDate: string
    deviceId: number
}

export interface LSCustomerInfo {
    deviceId: number
}

export enum OrderStatus {
    waiting = 'waiting',
    queueing = 'queueing',
    preparing = 'preparing',
    completed = 'completed'
}

export enum CRUD {
    create = 'create',
    createWithInsert = 'createWithInsert',
    reading = 'reading',
    update = 'update',
    delete = 'delete',
    orderId = 'orderId'
}