
import { defineComponent, ref, getCurrentInstance } from "vue";
import jsonToObject from "./mixins/forLoopMixin";
import {
  System,
  Banner,
  Category,
  Item,
  LSCustomerInfo,
  CRUD,
  OrderStatus,
  Order,
} from "@/interface";
import ItemDetailComponent from "./components/ItemDetailComponent.vue";
import FooterComponent from "./components/FooterComponent.vue";
import OrderListModalComponent from "./components/OrderListModalComponent.vue";
import AlertModalComponent from "./components/AlertModalComponent.vue";
import OrderHistoryModalComponent from "./components/OrderHistoryModalComponent.vue";
import {
  getCustomerInfoFromLS,
  removeOrderHistoryFromLS,
  setOrderHistoryToLS,
} from "./mixins/localStorageMixin";

export default defineComponent({
  components: {
    ItemDetailComponent,
    FooterComponent,
    OrderListModalComponent,
    AlertModalComponent,
    OrderHistoryModalComponent,
  },
  setup() {
    const { proxy } = getCurrentInstance() as any;
    const proxyData = ref({
      url: proxy.$getUrl,
      fetchCode: proxy.$fetchCode,
    });

    let systemData = ref<System>({
      id: 1,
      companyName: "...",
      companyAddress: "",
      companyContact: "",
      companyDescription: "",
      serviceCharge10Percent: false,
      tax6Percent: false,
    });
    let banners = ref<Banner[]>([
      { id: 1, image: "", description: "", itemId: 0 },
    ]);
    let categories = ref<Category[]>([]);
    let items = ref<Item[]>([]);
    let orderList = ref<Item[]>([]);
    let orderHistories = ref<Order[]>([]);
    let customerInfo = ref<LSCustomerInfo | null>();
    let isLoading = ref<boolean>(true);

    let fetchCategories = fetch(
      `${proxyData.value.url}tqx=out:json&sheet=Categories`
    );
    let fetchBanners = fetch(
      `${proxyData.value.url}tqx=out:json&sheet=Banners`
    );
    let fetchItems = fetch(`${proxyData.value.url}tqx=out:json&sheet=Items`);
    let fetchSystem = fetch(`${proxyData.value.url}tqx=out:json&sheet=System`);
    let fetchOrderHistories;

    const dataCustomerInfo = getCustomerInfoFromLS();
    if (dataCustomerInfo) {
      fetchOrderHistories = fetch(
        `${proxyData.value.url}sheet=Orders&tq=${encodeURIComponent(
          "select * where K = "
        )}${dataCustomerInfo.deviceId} and C != ${encodeURIComponent(
          `"${OrderStatus.completed}"`
        )}`
      );
      customerInfo.value = dataCustomerInfo;
    }

    Promise.all([
      fetchCategories,
      fetchBanners,
      fetchItems,
      fetchSystem,
      fetchOrderHistories,
    ]).then((res) => {
      // res[0] he res[1] 不知道为什么不能一起 run， 所以只好层中层
      // categories => banners => items
      res[0].text().then((rep) => {
        const dataCategories = jsonToObject(rep);
        dataCategories.forEach((c: Category) => (c.items = []));

        // banners
        res[1].text().then((rep) => {
          const dataBanners = jsonToObject(rep);

          // items
          res[2].text().then((rep) => {
            const dataItems = jsonToObject(rep);
            dataItems.forEach((item: Item) => {
              const foundCategory = dataCategories.find(
                (c: Category) => c.name === item.category
              );

              item.added = 0;
              foundCategory.items.push(item);

              dataBanners.forEach((banner: Banner) => {
                if (banner.itemId === item.id) {
                  banner.item = item;
                }
              });
            });

            categories.value = dataCategories;
            banners.value = dataBanners;
            items.value = dataItems;
            orderList.value = []; // dataOrderList
            isLoading.value = false;
          });
        });
      });

      // system
      res[3].text().then((rep) => {
        systemData.value = jsonToObject(rep)[0];
        window.document.title = `${systemData.value.companyName} - Menu Panda`;
      });

      // orderHistories
      if (res[4]) {
        res[4].text().then((rep) => {
          const dataOrderHistories = jsonToObject(rep);
          dataOrderHistories.forEach((order: Order) => {
            order.items = JSON.parse(order.items);
          });
          orderHistories.value = dataOrderHistories;
        });
      }
    });

    return {
      proxyData,
      systemData,
      banners,
      categories,
      items,
      orderList,
      orderHistories,
      customerInfo,
      isLoading,
    };
  },
  data() {
    return {
      diplayItemId: null as null | number,
      orderList: [] as Item[],
      isShowOrderListModal: false,
      isShowAlertModal: false,
      isShowOrderHistoryModal: false,
      orderId: 0,
    };
  },
  computed: {
    totalAmount() {
      let result = 0;
      this.orderList.forEach(
        (item: Item) => (result += item.price * item.added)
      );
      return result;
    },
  },
  methods: {
    showItemDetail(id: number): void {
      this.diplayItemId = id;
    },
    closeItemDetail() {
      this.diplayItemId = null;
    },
    addToOrder(value: { id: number; qty: number }) {
      const item = this.items.find((item: Item) => item.id === value.id)!;
      item.added += value.qty;

      const foundInOrderList = this.orderList.find(
        (item: Item) => item.id === value.id
      );
      if (!foundInOrderList) {
        this.orderList.unshift(item);
      }

      this.orderList.forEach((item: Item, index) => {
        if (item.id === value.id && item.added === 0) {
          this.orderList.splice(index, 1);
        }
      });

      setOrderHistoryToLS(this.orderList);
    },
    showOrderListModal(value: boolean) {
      this.isShowOrderListModal = value;
    },
    removeOrderItem(id: number) {
      this.items.find((item: Item) => item.id === id)!.added = 0;

      let indexOf = this.orderList.findIndex((item: Item) => item.id === id);
      this.orderList.splice(indexOf, 1);

      if (this.orderList.length > 0) {
        setOrderHistoryToLS(this.orderList);
      } else {
        removeOrderHistoryFromLS();
      }
    },
    orderSubmitted() {
      removeOrderHistoryFromLS();

      fetch(
        `https://script.google.com/macros/s/${this.proxyData.fetchCode}/exec`,
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          headers: {
            "Content-Type": "application/json",
          },
          redirect: "follow",
          body: JSON.stringify({ crud: CRUD.orderId, sheet: "Orders" }),
        }
      ).then(() => {
        fetch(
          `${this.proxyData.url}sheet=Orders&tq=${encodeURIComponent(
            "select * where K = "
          )}${this.customerInfo!.deviceId} and C = ${encodeURIComponent(
            `"${OrderStatus.waiting}"`
          )}`
        )
          .then((res) => res.text())
          .then((rep) => {
            const datas: Order[] = jsonToObject(rep);
            if (datas.length > 1) {
              alert("Error!, Order Id should not empty more than 2 cells!");
            }
            this.orderId = datas[0].orderId!;
          });
      });

      setTimeout(() => {
        this.isShowOrderListModal = false;
        (this.orderList = []),
          this.items.forEach((item: Item) => (item.added = 0));
      }, 1000);
      setTimeout(() => {
        this.isShowAlertModal = true;
      }, 2000);
    },
    showAlertModal(value: boolean) {
      this.isShowAlertModal = value;
    },
    showOrderHistoryModal(value: boolean) {
      this.isShowOrderHistoryModal = value;
    },
  },
  watch: {
    orderHistories(newValue, oldValue) {
      if (newValue.length !== oldValue.length) {
        setTimeout(() => {
          this.isShowOrderHistoryModal = true;
        }, 1000);
      }
    },
  },
});
