export default function jsonToObject(rep: any) {
    const data = JSON.parse(rep.substring(47).slice(0, -2));
    const cols = data.table.cols;

    if (data.table.parsedNumHeaders === 0) {
        return [];
    }

    return data.table.rows.map((row: any) => {
        const obj = {} as any;
        row.c.forEach((data: any, index: number) => {
            // 下划线是私有，目前用在 Google Sheet 的显示图片
            if (cols[index].label.substring(0, 1) === "_") {
                return
            }
            // data === null 是因为 Google Sheet 的来源是空
            // 如果数字是空的话，就一定要用 0 ，这是为了作为区分
            obj[cols[index].label] = data === null ? '' : data.v;
        });

        return obj;
    });
}
