
import { defineComponent } from "vue";

export default defineComponent({
  props: ["isShowAlertModal", "orderId"],
  emits: ["showAlertModal"],
  methods: {
    padStart(val: number) {
      return val.toString().padStart(3, "0");
    },
    showAlertModal(value: boolean) {
      this.$emit("showAlertModal", value);
      setTimeout(() => {
        window.location.reload();
      }, 1000);
    },
  },
});
