import { Item, LSCustomerInfo } from "@/interface";

export enum LocalStorage {
    menuPandaCustomerInfo = 'menuPandaCustomerInfo',
    menuPandaOrderHistory = 'menuPandaOrderHistory',
}

export function setCustomerInfoToLS(value: LSCustomerInfo) {
    const data = JSON.stringify(value);
    localStorage.setItem(LocalStorage.menuPandaCustomerInfo, data);
}
export function getCustomerInfoFromLS(): LSCustomerInfo | null {
    return JSON.parse(
        localStorage.getItem(LocalStorage.menuPandaCustomerInfo)!
    );
}
export function removeCustomerInfoFromLS() {
    localStorage.removeItem(LocalStorage.menuPandaCustomerInfo)
}

export function setOrderHistoryToLS(values: Item[]) {
    const datas = JSON.stringify(values);
    localStorage.setItem(LocalStorage.menuPandaOrderHistory, datas);
}
export function getOrderHistoryFromLS(): Item[] | null {
    return JSON.parse(
        localStorage.getItem(LocalStorage.menuPandaOrderHistory)!
    );
}
export function removeOrderHistoryFromLS() {
    localStorage.removeItem(LocalStorage.menuPandaOrderHistory)
}
