
import { CRUD, Order, OrderStatus } from "@/interface";
import fetchPost from "@/mixins/fetchMixin";
import { formatToGoogleSheetDate } from "@/mixins/formatDateMixin";
import { rounding } from "@/mixins/generalMixin";
import {
  getCustomerInfoFromLS,
  setCustomerInfoToLS,
} from "@/mixins/localStorageMixin";
import { defineComponent } from "vue";

export default defineComponent({
  props: [
    "isShowOrderListModal",
    "orderList",
    "totalAmount",
    "systemData",
    "proxyData",
    "customerInfo",
  ],
  emits: ["showOrderListModal", "removeOrderItem", "orderSubmitted"],
  data() {
    return {
      loading: false,
      dotDotDot: "...",
    };
  },
  computed: {
    serviceCharge() {
      return this.systemData.serviceCharge10Percent
        ? this.totalAmount * 0.1
        : 0;
    },
    tax() {
      return this.systemData.tax6Percent ? this.totalAmount * 0.06 : 0;
    },
    roundingAdj() {
      const serviceCharge = parseFloat(this.serviceCharge.toFixed(2));
      const tax = parseFloat(this.tax.toFixed(2));
      return rounding(this.totalAmount + serviceCharge + tax);
    },
    nettTotal() {
      return (
        this.totalAmount + this.serviceCharge + this.tax + this.roundingAdj
      );
    },
  },
  methods: {
    showOrderListModal(value: boolean) {
      this.$emit("showOrderListModal", value);
    },
    removeOrderItem(id: number) {
      this.$emit("removeOrderItem", id);
      if (this.orderList.length === 0) {
        this.showOrderListModal(false);
      }
    }, 
    confirmOrder() {
      let deviceId = getCustomerInfoFromLS()
        ? getCustomerInfoFromLS()!.deviceId
        : null;
      let order: Order;

      this.loading = true;
      const timer = setInterval(() => {
        const dotCount = this.dotDotDot.length;
        if (dotCount === 0) {
          this.dotDotDot = ".";
        } else if (dotCount === 1) {
          this.dotDotDot = "..";
        } else if (dotCount === 2) {
          this.dotDotDot = "...";
        } else if (dotCount === 3) {
          this.dotDotDot = "";
        }
      }, 1000);

      if (!deviceId) {
        const dateNow = Date.now();
        setCustomerInfoToLS({ deviceId: dateNow });
        deviceId = dateNow;
      }

      order = {
        id: 0, // 0 = get max id from GS
        status: OrderStatus.waiting,
        tableNo: 0,
        items: JSON.stringify(this.orderList),
        amount: parseFloat(this.nettTotal.toFixed(2)),
        createdDate: formatToGoogleSheetDate(new Date()),
        deviceId,
      };

      fetchPost<Order>(
        CRUD.createWithInsert,
        "Orders",
        this.proxyData.fetchCode,
        order
      )
        .then(() => {
          this.loading = false;
          clearInterval(timer);
          this.$emit("orderSubmitted");
        })
        .catch(() => {
          alert("Fail to submit, please inform waiters!");
        });
    },
  },
});
